import { Injectable } from '@angular/core';
import { dispatch } from '@angular-redux/store';
import { SET_ONLINE_STATUS, SET_SELECTED_ARM, SET_TRIGGER, SET_VIEW_MODE } from './index';

@Injectable()
export class Actions {

  static readonly VIEW_MODE_DRIVE = 'drive';
  static readonly VIEW_MODE_ARM = 'arm';
  static readonly VIEW_MODE_VIDEO = 'video';

  @dispatch()
  setOnlineStatus(status: boolean) {
    return {
      type: SET_ONLINE_STATUS,
      payload: status
    }
  }

  @dispatch()
  setTriggerStatus(status: boolean) {
    return {
      type: SET_TRIGGER,
      payload: status
    }
  }

  @dispatch()
  setViewMode(mode: string) {
    return {
      type: SET_VIEW_MODE,
      payload: mode
    }
  }

  @dispatch()
  setSelectedArm(arm: number) {
    return {
      type: SET_SELECTED_ARM,
      payload: arm
    }
  }

}



