import { Component } from '@angular/core';
import { Actions } from '../store/actions';
import { Observable } from 'rxjs/Observable';
import { select } from '@angular-redux/store';

@Component({
  selector: 'app-main-screen',
  templateUrl: './main-screen.component.html',
  styleUrls: ['./main-screen.component.scss']
})
export class MainScreenComponent {

  @select() viewMode$: Observable<string>;
  @select() selectedArm$: Observable<number>;

  viewMode = '';

  readonly VIEW_MODE_ARM = Actions.VIEW_MODE_ARM;
  readonly VIEW_MODE_DRIVE = Actions.VIEW_MODE_DRIVE;

  constructor(private actions: Actions) {
    this.viewMode$.subscribe(mode => this.viewMode = mode);
  }

  onArmSelect(value) {
    this.actions.setSelectedArm(value);
  }

  onSwitchChange(val) {
    if (val) {
      this.actions.setViewMode(Actions.VIEW_MODE_ARM);
    } else {
      this.actions.setViewMode(Actions.VIEW_MODE_DRIVE);
    }
  }

  onTrigger(val: boolean) {
    this.actions.setTriggerStatus(val);
  }

}



