import { Component, ElementRef, Input, OnChanges, OnInit, SimpleChange, SimpleChanges, ViewChild } from '@angular/core';
import { ControllerService, IControllerData } from './controller.service';
@Component({
  selector: 'app-controller',
  templateUrl: './controller.component.html',
  styleUrls: ['./controller.component.scss']
})
export class ControllerComponent implements OnInit, OnChanges {

  @Input() isArmMode = false;
  @Input() selectedArm: number;
  @ViewChild('manager') private managerElRef: ElementRef;

  private manager: any;
  data: IControllerData = this.resetData();
  isActive: boolean;

  constructor(private controllerServ: ControllerService) {}

  ngOnInit() {

    /**
     * Inicjalizacja i konfiguracja joysticka
     * http://yoannmoinet.github.io/nipplejs/
     */
    this.manager = window['nipplejs'].create({
      zone: this.managerElRef.nativeElement,
      color: 'black',
      position: {
        top: '0',
        left: '0'
      },
      size: this.controllerServ.getJoystickSize(),
      mode: 'static',
      restOpacity: 1
    });

    /**
     * Podpiecie eventow listenerow
     */
    this.manager.on('move' , this.onMove.bind(this));
    this.manager.on('end' , this.onEnd.bind(this));
    this.manager.on('start' , this.onStart.bind(this));

  }

  ngOnChanges(changes: SimpleChanges) {

    if (changes.isArmMode) {
      this.controllerServ.enableDrive(!changes.isArmMode.currentValue);
    }

    if (changes.selectedArm) {
      this.controllerServ.selectArm(changes.selectedArm.currentValue);
    }

  }

  /**
   * Pocztkowy stan obiektu z danymi controllera
   * @returns {{x: number, y: number, distance: number, angle: number}}
   */
  private resetData(): IControllerData {
    return {
      x: 0,
      y: 0,
      distance: 0,
      angle: 0
    };
  }

  /**
   * Metoda odpalana po ruchu joysticka, ustawia distance
   * @param evt
   * @param data
   * @description
   * Przykadowy obiekt data przekazywany do funkcji
    {
      identifier: 0,              // the identifier of the touch/mouse that triggered it
      position: {                 // absolute position of the center in pixels
          x: 125,
          y: 95
      },
      force: 0.2,                 // strength in %
      distance: 25.4,             // distance from center in pixels
      pressure: 0.1,              // the pressure applied by the touch
      angle: {
          radian: 1.5707963268,   // angle in radian
          degree: 90
      },
      instance: Nipple            // the nipple instance that triggered the event
    }
   */
  private onMove(evt, {distance, angle})  {
    this.controllerServ.onMove(distance, angle.radian);
  }

  /**
   * Metoda odpalana po puszczeniu joysticka, resetuje dane
   *
   */
  private onEnd() {
    this.controllerServ.onEnd();
    this.isActive = false;
  }

  private onStart() {
    this.isActive = true;
  }

}



