import {
  IAppState, INIT_STATE, SET_BATTERY_STATUS, SET_ONLINE_STATUS, SET_SAFE_BUTTON_STATUS, SET_SELECTED_ARM,
  SET_TRIGGER, SET_VIEW_MODE
} from './index';

export const rootReducer = function (state: IAppState = INIT_STATE, action) {

  const newState = Object.assign({}, state);

  switch (action.type) {
    case SET_ONLINE_STATUS :
      newState.isOnline = action.payload;
      return newState;

    case SET_BATTERY_STATUS :
      newState.batteryStatus = action.payload;
      return newState;

    case SET_SAFE_BUTTON_STATUS :
      newState.isSafeButton = action.payload;
      return newState;

    case SET_SELECTED_ARM :
      newState.selectedArm = action.payload;
      return newState;

    case SET_TRIGGER :
      newState.isTrigger = action.payload;
      return newState;

    case SET_VIEW_MODE :
      newState.viewMode = action.payload;
      return newState;

    default:
      return state;
  }

};





