import { Injectable } from '@angular/core';
import { RosCommunicationService } from '../../commons/ros-communication.service';

export interface IControllerData {
  x: number;
  y: number;
  distance: number;
  angle: number;
}

const getArmData = () => [127, 127, 127, 127, 127, 127, 127, 127];

const JOYSTICK_SIZE = 222;

@Injectable()
export class ControllerService {

  /**
   * Numer wybranaego ramienia.
   */
  private selectedArm = 1;
  /**
   * Stan przecznika JAZDA/RAMI.
   */
  private isDriveModeEnabled = true;

  constructor(private ros: RosCommunicationService) { }

  /**
   * Ustawia numer wybranego ramienia.
   * @param selectedArm numer wybranego ramienia
   */
  selectArm(selectedArm) {
    this.selectedArm = selectedArm;
  }

  /**
   * Ustawia stan przecznika JAZDA/RAMI
   * @param value czy wybrano jazd
   */
  enableDrive(value) {
    this.isDriveModeEnabled = value;
  }

  onEnd() {
    this.ros.notify('STOP');
  }

  onMove(distance, angleRad) {
    distance = distance && this.getNormalizedDistance(distance);
    let x: number = Math.floor(this.convertDataToX(angleRad, distance));
    let y: number = Math.floor(this.convertDataToY(angleRad, distance));

    const armData = getArmData();

    if (!this.isDriveModeEnabled) {

      if (this.selectedArm === 1) {
        armData[0] =  Math.floor((x + 255) / 2);
        armData[1] =  Math.floor((y + 255) / 2);
      } else if (this.selectedArm === 2) {
        armData[2] =  Math.floor((x + 255) / 2);
        armData[3] =  Math.floor((y + 255) / 2);
      } else if (this.selectedArm === 3) {
        armData[4] =  Math.floor((x + 255) / 2);
        armData[5] =  Math.floor((y + 255) / 2);
      } else if (this.selectedArm === 4) {
        armData[6] =  Math.floor((x + 255) / 2);
        armData[7] =  Math.floor((y + 255) / 2);
      }

      x = 0;
      y = 0;
    }

    this.ros.notify('STEERING', {
      DRIVE: [x, y],
      ARM: armData
    });
  }

  /**
   * Zwraca sta JOYSTICK_SIZE
   * @returns {number}
   */
  getJoystickSize(): number {
    return JOYSTICK_SIZE;
  }

  /**
   * Zamienia wychylenie joysticka na warto od -255 do 255
   * @param data {IControllerData}
   */
  private getNormalizedDistance(distance: number): number {
    const maxDistance = this.getJoystickSize() / 2;
    return distance / maxDistance * 255;
  }

  /**
   * Wylicza pozycj x z kta i wychylenia joysticka.
   * @param angleRad kt w radianach
   * @param distance odlego joysticka od rodka
   */
  private convertDataToX(angleRad, distance): number {
    return Math.floor(distance * Math.cos(angleRad));
  }

  /**
   * Wylicza pozycj y z kta i wychylenia joysticka.
   * @param angle kt w radianach
   * @param distance odlego joysticka od rodka
   */
  private convertDataToY(angle, distance): number {
    return Math.floor(distance * Math.sin(angle));
  }
}





