import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import 'hammerjs';
import { AppComponent } from './app.component';
import { MdSlideToggleModule } from '@angular/material';
import { ControllerComponent } from './main-screen/controller/controller.component';
import { LoadingScreenComponent } from './loading-screen/loading-screen.component';
import { MainScreenComponent } from './main-screen/main-screen.component';
import { VideoComponent } from './main-screen/video/video.component';
import { RouterModule } from '@angular/router';
import { RosCommunicationService } from './commons/ros-communication.service';
import { SwitchModeComponent } from './main-screen/switch-mode/switch-mode.component';
import { StatusBarComponent } from './main-screen/status-bar/status-bar.component';
import { TriggerComponent } from './main-screen/trigger/trigger.component';
import { BatteryStatusComponent } from './commons/battery-status/battery-status.component';
import { ConnectionStatusComponent } from './commons/connection-status/connection-status.component';
import { HudComponent } from './main-screen/hud/hud.component';
import { SpeedComponent } from './main-screen/hud/speed/speed.component';
import { MoveTriggerComponent } from './main-screen/move-trigger/move-trigger.component';
import { CompassComponent } from './main-screen/hud/compass/compass.component';
import { ControllerService } from './main-screen/controller/controller.service';
import { ArtificialHorizonComponent } from './main-screen/hud/artificial-horizon/artificial-horizon.component';
import { GpsComponent } from './main-screen/hud/gps/gps.component';
import { StoreModule } from './store/store.module';

const APP_ROUTES = [{path: '', component: LoadingScreenComponent}, {path: 'main', component: MainScreenComponent}];

@NgModule({
  declarations: [
    AppComponent,
    ControllerComponent,
    LoadingScreenComponent,
    MainScreenComponent,
    VideoComponent,
    SwitchModeComponent,
    StatusBarComponent,
    TriggerComponent,
    BatteryStatusComponent,
    BatteryStatusComponent,
    ConnectionStatusComponent,
    HudComponent,
    SpeedComponent,
    MoveTriggerComponent,
    CompassComponent,
    ArtificialHorizonComponent,
    GpsComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpModule,
    MdSlideToggleModule,
    RouterModule.forRoot(APP_ROUTES, {useHash: true}),
    StoreModule
  ],
  providers: [
    RosCommunicationService,ControllerService
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



