import { Component, EventEmitter, Output, Input, OnChanges, OnInit } from '@angular/core';

@Component({
  selector: 'app-trigger',
  templateUrl: './trigger.component.html',
  styleUrls: ['./trigger.component.scss']
})
export class TriggerComponent implements OnInit, OnChanges {

  @Output() onEvent = new EventEmitter<boolean>();
  @Input('buttonClasses') buttonClasses: any = {};
  @Input('isDisable') isDisable: boolean;

  classes: any = {};

  constructor() { }

  ngOnInit() {

  }
  ngOnChanges() {
    if (this.buttonClasses) {
      this.classes = Object.assign(this.classes, this.buttonClasses);
    }
  }

  onMouseDown(event) {
    event.stopPropagation();
    this.classes['is-mouse-down'] = true;
    this.onEvent.emit(true);
  }

  onMouseUp(event) {
    event.stopPropagation();
    this.classes['is-mouse-down'] = false;
    this.onEvent.emit(false);
  }

}



