import { Component, OnInit } from '@angular/core';
import { RosCommunicationService } from '../../../commons/ros-communication.service';

@Component({
  selector: 'app-gps',
  templateUrl: './gps.component.html',
  styleUrls: ['./gps.component.scss']
})
export class GpsComponent implements OnInit {

  /** dugo */
  lon: string;
  /** szeroko */
  lat: string;

  constructor(private ros: RosCommunicationService) {
    ros.on('POSITION').subscribe(({ GPS }) => {
      this.lon = Math.abs(GPS[0]).toString().replace('.', '') + (GPS[0] >= 0 ? " N" : " S");
      this.lat = Math.abs(GPS[1]).toString().replace('.', '') + (GPS[1] >= 0 ? " E" : " W");
      //TODO dokoczy przeliczanie uamka na minuty i sekundy, przykad:
      // 51.776411, 19.425318   =   5146'35.1"N 1925'31.1"E
    });
  }

  ngOnInit() {
  }

}


