import { Component, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'app-move-trigger',
  templateUrl: './move-trigger.component.html',
  styleUrls: ['./move-trigger.component.scss']
})
export class MoveTriggerComponent {

  buttons = [
    {
      cssClass: 'm-button m-button--1 app-pos--abs--top-left'
    },
    {
      cssClass: 'm-button m-button--2 app-pos--abs--top-right'
    },
    {
      cssClass: 'm-button m-button--4 app-pos--abs--bottom-left'
    },
    {
      cssClass: 'm-button m-button--3 app-pos--abs--bottom-right'
    }
  ];

  @Input() selectedArm: number;
  @Output() armChange = new EventEmitter<number>();

  onMouseDown(event, i) {
    event.stopPropagation();

    if (this.selectedArm !== i) {
      this.selectedArm = i;
      this.armChange.emit(this.selectedArm);
    }
  }

}



