import { Component, OnInit } from '@angular/core';
import { RosCommunicationService } from '../../commons/ros-communication.service';

@Component({
  selector: 'app-battery-status',
  templateUrl: './battery-status.component.html',
  styleUrls: ['./battery-status.component.scss']
})
export class BatteryStatusComponent implements OnInit {

  batteryValue: number = 4;
  lowBattery: boolean = false;

  constructor(private ros: RosCommunicationService) {

    ros.on('TELEMETRY').subscribe(({ BATTERY }) => {
      if (BATTERY > 75) {
        this.batteryValue = 4;
      } else if (BATTERY > 50) {
        this.batteryValue = 3;
      } else if (BATTERY > 25) {
        this.batteryValue = 2;
      } else {
        this.batteryValue = 1;
      }
      this.lowBattery = BATTERY <= 10;
    });


  }

  ngOnInit() {
  }

}



