import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import { Router } from '@angular/router';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { Actions } from '../store/actions';

type CommunicationTopic = 'STEERING' | 'POSITION' | 'TELEMETRY' | 'TRIGGER' | 'CONNECTION' | 'STOP';
type CommunicationSubjects = Partial<Record<CommunicationTopic, any>>;

const ROSLIB = window['ROSLIB'];

// hycom_msgs
const PREFIX = 'hycom_msgs';

@Injectable()
export class RosCommunicationService {

  isConnecting = false;

  private ros;
  private intervalId;
  private subjects: CommunicationSubjects = {
    STEERING: new Subject(),
    POSITION: new Subject(),
    TELEMETRY: new Subject(),
    TRIGGER: new Subject(),
    CONNECTION: new Subject(),
    STOP: new Subject()
  };

  constructor(private router: Router,
              private actions: Actions) {
    this.ros = new ROSLIB.Ros();

    this.ros.on('error', () => {
      this.actions.setOnlineStatus(false);

      if (!this.isConnecting) {
        console.log('errror');
        setTimeout(this.connectToRos.bind(this), 3000);
      }



      if (this.router.url !== '/') {
        this.router.navigate(['']);
      }

    });

    // Find out exactly when we made a connection.
    this.ros.on('connection', (test) => {
      this.actions.setOnlineStatus(true);

      this.isConnecting = false;

    });

    this.ros.on('close', () => {
      this.actions.setOnlineStatus(false);

      if (!this.isConnecting) {
        console.log('close');
        setTimeout(this.connectToRos.bind(this), 3000);
      }




      if (this.router.url !== '/') {
        this.router.navigate(['']);
      }
    });

    const steeringTopic = new ROSLIB.Topic({
      ros: this.ros,
      name: '/STEERING',
      messageType: `${PREFIX}/STEERING`
    });

    /**
     * Nasuchuje na zmiany joysticka
     */
    this.on('STEERING')
    // .debounceTime(200) // przepuszcza wartosci co 10ms
      .subscribe(({DRIVE, ARM}) => {
        const steeringMessage = new ROSLIB.Message({DRIVE, ARM});

        this.repeatAction(() => {
          console.log(JSON.stringify(steeringMessage));
          steeringTopic.publish(steeringMessage);
        });
      });

    this.on('STOP').subscribe(() => {
      clearInterval(this.intervalId);
      const steeringMessage = new ROSLIB.Message({
        DRIVE: [0, 0],
        ARM: [127, 127, 127, 127, 127, 127, 127, 127]
      });
      steeringTopic.publish(steeringMessage);

    });

    this.connectToRos();
  }

  public connectToRos() {

    this.isConnecting = true;

    this.ros.connect('ws://192.168.1.117:9090');
    // this.ros.connect('ws://raptors.hycom.pl/app');
    // this.ros.connect('wss://localhost:9090');

    const listenerPosition = new ROSLIB.Topic({
      ros: this.ros,
      name: '/POSITION',
      messageType: `${PREFIX}/POSITION`
    });

    listenerPosition.subscribe((msg) => {
      console.log(msg);
      this.notify('POSITION', msg);
    });

    const listenerTelemetry = new ROSLIB.Topic({
      ros: this.ros,
      name: '/TELEMETRY',
      messageType: `${PREFIX}/TELEMETRY`
    });

    listenerTelemetry.subscribe((msg) => {
      console.log(msg);
      this.notify('TELEMETRY', msg);
      if (msg.SAFETY == 1) {
        //azik zatrzymany przyciskiem
        this.router.navigate(['']);
      }
    });
  }

  private repeatAction(action) {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
    action();
    this.intervalId = setInterval(action, 200);
  };

  /**
   * Metoda pozwala nasluchiwac na okreslonym kanale komunikacyjnym
   * @param topic {CommunicationTopic} nazwa kanau
   * @returns {Observable<T>} obiekt observable
   */
  on(topic: CommunicationTopic): Observable<any> {
    return this.subjects[topic].asObservable();
  }

  /**
   *
   * @param topic {CommunicationTopic} - nazwa kanau
   * @param data {any} Dane przekazywane na dany kana komunikacyjny
   */
  notify(topic: CommunicationTopic, data?: any) {
    this.subjects[topic].next(data);
  }

}



